import React from "react";
import dayjs from "dayjs";
import style from "assets/scss/style.module.scss";
import noimg from "assets/img/noimg.png";

/**
 *          
 * @param documents -  
 * @param thumb -    
 * @return
 */

const ListView = ({ documents, thumb, inview }) => {
    console.log(documents);
    return (
        <ul className={style.mediaList}>
            {/**      */}
            {documents?.length > 0 ? (
                documents.map((item, index) => (
                    <li
                        className={style.mediaItem}
                        key={index}
                        {...(documents.length - 1 === index
                            ? { ref: inview }
                            : {})}
                    >
                        <a
                            href={item.url}
                            target="_blank"
                            rel="noreferrer"
                            className={thumb && style.thumbnail}
                        >
                            {thumb && (
                                <img
                                    src={
                                        item.thumbnail ? item.thumbnail : noimg
                                    }
                                    onError={(e) =>
                                        (e.currentTarget.src = noimg)
                                    }
                                    alt={item.title}
                                />
                            )}
                            <h2
                                className={style.mediaHeading}
                                dangerouslySetInnerHTML={{ __html: item.title }}
                            />
                            <p
                                className={style.desc}
                                dangerouslySetInnerHTML={{
                                    __html: item.contents,
                                }}
                            />

                            {item.price && (
                                <p className={style.price}>
                                    : <span>{item.price} </span>
                                    : <span>{item.sale_price}</span>
                                </p>
                            )}

                            <p className={style.date}>
                                {item.authors && (
                                    <span>
                                        <strong>
                                            {item.authors.join(",")}
                                        </strong>
                                    </span>
                                )}
                                {item.publishers && (
                                    <span>
                                        <strong>{item.publishers}</strong>
                                    </span>
                                )}
                                {item.cafename && (
                                    <span>
                                        <strong>{item.cafename}</strong>
                                    </span>
                                )}
                                {item.blogname && (
                                    <span>
                                        <strong>{item.blogname}</strong>
                                    </span>
                                )}
                                {item.datetime && (
                                    <span>
                                        <strong>
                                            {dayjs(item.datetime).format(
                                                "YYYY-MM-DD hh:mm"
                                            )}
                                        </strong>
                                    </span>
                                )}
                            </p>
                        </a>
                    </li>
                ))
            ) : (
                <div>  .</div>
            )}
        </ul>
    );
};

export default ListView;
